import { getToken } from '@/utils/auth'
import request from '@/utils/request'

const getDefaultState = () => {
    return {
        userlist: {
            list: []
        }
    }
}

const state = getDefaultState()

const mutations = {
    RESET_STATE: (state) => {
        Object.assign(state, getDefaultState())
    },
    SET_STATE: (state, data) => {
        Object.assign(state.userlist, data)
    },
    ADD_USER: (state, data) => {
        state.userlist.list.unshift(data)
        if (state.userlist.list.length > state.userlist.limit) {
            if (state.userlist.total_page <= 1) {
                state.userlist.total_page = Math.ceil(state.userlist.list.length / state.userlist.limit)
            }
            state.userlist.list.pop()
        }
    }
}

const actions = {
    UserList({ commit }, params) {
        return new Promise((resolve, reject) => {
            request({
                url: 'UserManage/user/list',
                method: 'get',
                params: params
            }).then(response => {
                // 此处response是request.js中request()返回的res 即data = res.data
                const { data } = response
                commit('SET_STATE', data)
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    freezeUser({ commit }, freezeData) {
        return new Promise((resolve, reject) => {
            request({
                url: 'UserManage/user/freeze',
                method: 'post',
                data: freezeData
            }).then(response => {
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    unfreezeUser({ commit }, uid) {
        return new Promise((resolve, reject) => {
            request({
                url: 'UserManage/user/unfreeze',
                method: 'post',
                data: { uid: uid }
            }).then(response => {
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    addUser({ commit }, data) {
        data.token = getToken()
        return new Promise((resolve, reject) => {
            request({
                url: 'usermanage/adduser',
                method: 'post',
                data: data
            }).then(response => {
                const { data } = response
                commit('ADD_USER', data)
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    },
    resetUser({ commit }, uid) {
        return new Promise((resolve, reject) => {
            request({
                url: 'UserManage/user/reset',
                method: 'post',
                data: { uid: uid }
            }).then(response => {
                resolve(response)
            }).catch(error => {
                reject(error)
            })
        })
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}



